export default{
	props:{
		searchValue:String,
	},
	data () {
		return {
			searchValueInput:'',
		}
	},
	methods:{
		onInputSearchValue(e){
			this.searchValueInput = e.detail.value;
		},
		onblurInputSearchValue(){ 
			this.$emit('clickSearch',{searchValue:this.searchValueInput});
		},
	}
}
